#page-landing{
     width: 100vw;
     height: 100vh;

     display: flex;
     justify-content: center;
     align-items: center;

     color: var(--color-text-in-primary);
     background: var(--color-primary);
}

.logo-container img{
    height: 9.6rem;
}
.hero-image{
     width:100%;
}

.logo-container{
     text-align: center;
     margin-bottom: 3.2rem
}
.logo-container h2{
    font-weight:500;
    font-size:1.9rem;
    line-height: 3.0rem;
    margin-top:0.8rem;
}

.buttons-container{
    display: flex;
    justify-content:center;
    margin:3.2rem 0;
}

.buttons-container a{
    width: 30rem;
    height: 7.4rem;
    border-radius: 0.8rem;
    
    font: 700 2.0rem Archivo;

    display: flex;
    align-items: center;
    justify-content:center;

    text-decoration: none;

    color: var(--color-button-text);

    transition: background-color 0.2s;

}

.buttons-container a:first-child{
    margin-right: 1.6rem;
}

.buttons-container a img{
    width:4rem;
}

.buttons-container a.study{
    background: var(--color-primary-lighter);
}

.buttons-container a.give-classes{
    background: var(--color-secondary);
}


.buttons-container a.study:hover{
    background: var(--color-primary-light);
}

.buttons-container a.give-classes:hover{
    background: var(--color-secondary-dark);
}
.buttons-container a img{
    margin-right: 2.4rem;
}
.total-connections{
    font-size: 1.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.total-connections img{
    margin-left: 0.8rem;
}

@media(min-width: 1100px){
    #page-landing-content{
        max-width: 1100px;

        display:grid;
        grid-template-rows: 350px 1fr;
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-areas: 
        "logo hero hero"
        "buttons buttons total"
        ;
    }

    .logo-container{
        grid-area: logo;
        align-self: center;
        margin: 0;
        text-align: left;
    }
    .logo-container img{
        height: 100%;
        
    }
    .logo-container h2{
        text-align: initial;
        font-size: 3.6rem;
    }
    .hero-image{
        grid-area: hero;
        justify-self: end;
    }
    .buttons-container{
        grid-area: buttons;
        justify-content: flex-start;
    }
    .buttons-container a {
        font-size: 2.4rem;
    }
    .total-container{
        grid-area: total;
        justify-self: end;
    }
}